.movieContainer {
  display: flex;
  gap: 12px;
  margin-bottom: 12px;
}

.movieInfoContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.moviePoster {
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
